  color: var(--link--color--hover);
  text-decoration: var(--link--text-decoration--hover);
  text-decoration-thickness: var(--text-decoration-thickness, auto);
}
@media (hover: hover) {
  a:hover {
    color: var(--link--color--hover);
    text-decoration: var(--link--text-decoration--hover);
    text-decoration-thickness: var(--text-decoration-thickness, auto);
  }
}

